* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.welcome-section-container {
  height: 500px;
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
  position: relative;
}

/*LEFT*/

.welcome-text-container {
  height: 100%;
  width: 100%;
  background-color: #eee8db;
  position: relative;
}

.welcome-text-1,
.welcome-text-2 {
  font-family: "Roboto", system-ui;
  font-weight: bold;
  font-style: normal;
  font-size: 40px;
}

.welcome-text-1 {
  position: absolute;
  left: 200px;
  top: 100px;
}

.welcome-text-2 {
  position: absolute;
  left: 200px;
  top: 160px;
}

.welcome-text-3 {
  position: absolute;
  left: 200px;
  top: 220px;
  font-size: 20px;
  font-weight: 500;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 3px;
}

.searchbox-container {
  position: absolute;
  left: 200px;
  top: 300px;
  width: 450px;
  height: 65px;
  transition: height 0.5s ease;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.searchbox-container.expanded {
  height: 200px;
}

.searchbox-container input {
  height: 65px;
  width: 65%;
  border-radius: 30px;
  border: none;
  /*nya*/
  border: none;
  padding-left: 20px;
  outline: none;
  transition: border-color 0.3s;
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
  position: absolute;
  top: 0px;
  left: 0px;
}

.searchbox-container input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
}

.find-food-button {
  height: 65px;
  width: 35%;
  border: none;
  background-color: red;
  color: white;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  cursor: pointer;
  border-radius: 30px;
  transition: background-color 1s, color 1s;

  /*nya*/
  position: absolute;
  right: 0px;
  top: 0px;
}

.find-food-button:hover {
  transition: 0.5s;
  opacity: 0.8;
}

.find-food-button:active {
  opacity: 0.5;
  transition: opacity 0.3s;
}

.search-results {
  position: absolute;
  left: 0px;
  top: 60px;
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.option-div {
  padding: 5px;
  font-size: 14px;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}

.option-div.highlighted {
  background-color: #f0f0f0; /* Visar den markerade raden */
}

.option-div:hover {
  background-color: #f0f0f0; /* Hover-effekt */
}

/*RIGHT*/

.welcome-image-container {
  width: 50%;
  height: 100%;
  /*
background-color: #eee8db;
*/
  background-color: red;
  position: relative;
}

.welcome-image-1 {
  /*
height: 40%;
width: 40%;
*/
  height: 46%;
  width: 18%;
  border-radius: 50%;
  position: absolute;
  right: 240px;
  top: 130px;
}

.welcome-image-2 {
  height: 30%;
  width: 13%;
  border-radius: 50%;
  position: absolute;
  right: 530px;
  top: 10px;
}

.welcome-image-3 {
  /*
height: 27%;
width: 27%;
*/

  height: 30%;
  width: 13%;
  border-radius: 50%;
  position: absolute;
  right: 20px;
  bottom: 10px;
}

.welcome-image-4 {
  height: 30%;
  width: 13%;
  border-radius: 50%;
  position: absolute;
  right: 530px;
  bottom: 10px;
}

.welcome-image-5 {
  height: 30%;
  width: 13%;
  border-radius: 50%;
  position: absolute;
  right: 20px;
  top: 10px;
}

