.menu-section-container {
  width: 100%;
  max-width: 100vw;
  box-sizing: border-box;
  overflow: hidden;
  height: auto;
  padding-bottom: 100px;
  position: relative;
}

.menu-container-text {
  display: block;
  margin-bottom: 30px;
}

.menu-container-text p {
  margin-left: 100px;
  font-size: 30px;
  font-family: "Roboto", system-ui;
  font-weight: bold;
  font-style: normal;
}

.delete-icon-container {
  height: 30px;
  width: 30px;
  position: absolute;
  right: 2.5rem;
  top: 5px;
}

.delete-icon-container img {
  height: 100%;
  width: 100%;
}

.delete-filter-tooltip {
  height: 25px;
  width: 90px;
  position: absolute;
  right: 4rem;
  top: 5px;
  color: black;
  font-family: "Roboto", system-ui;
  font-style: normal;
  font-weight: bold;
  visibility: hidden;
  font-size: 17px;
  opacity: 0;
  transition: opacity 0.3s;
}

.delete-icon-container:hover .delete-filter-tooltip {
  display: block;
  visibility: visible; /* Gör den synlig */
  opacity: 1;
}

.menu-products-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 50px;
}

.pizza-menu-container,
.pasta-menu-container,
.asian-food-menu-container,
.mexican-food-menu-container,
.burgers-menu-container,
.wraps-menu-container,
.vegan-food-menu-container,
.mediterranean-food-menu-container {
  margin-left: 100px !important;
  width: 86%;
  height: 360px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 23px;
}

.menu-pizza-img-container,
.menu-pasta-img-container,
.menu-asian-food-img-container,
.menu-mexican-food-img-container,
.menu-burgers-img-container,
.menu-wraps-img-container,
.menu-vegan-food-img-container,
.menu-mediterranean-food-img-container {
  height: 360px;
  width: 305px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 10px #00000017;
}

.menu-pizza-img-container img,
.menu-pasta-img-container img,
.menu-asian-food-img-container img,
.menu-mexican-food-img-container img,
.menu-burgers-img-container img,
.menu-wraps-img-container img,
.menu-vegan-food-img-container img,
.menu-mediterranean-food-img-container img {
  height: 60%;
  width: 100%;
}

.name-stars-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50px;
}

.product-name {
  width: 75%;
  height: 100%;
  display: flex;
  align-items: center;
}

.product-name p {
  font-family: "Roboto", system-ui;

  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  margin-left: 5px;
}

.stars {
  width: 25%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stars img {
  height: 45%;
  width: 100%;
  margin-bottom: 2px;
  margin-right: 4px;
}

.product-text {
  width: 100%;
  height: 50px;
}

.product-text p {
  font-size: 16px;
  margin-left: 5px;
  font-family: "Poppins", sans-serif;
  font-style: normal;
}

.price-add-container {
  width: 100%;
  height: 40px;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.price-container {
  width: 60%;
}

.price-container {
  font-size: 23px;
  color: orange;
  margin-left: 5px;
}

.add-container {
  width: 40%;
  display: flex;
  height: 40px;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  border-radius: 25px;
}

.add-container {
  width: 35%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.add-minus-container {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: red;
  cursor: pointer;
}

.add-minus-container:hover {
  transition: 0.5s;
  opacity: 0.6;
}

.add-minus-container:active {
  opacity: 0.4;
  transition: opacity 0.3s;
}

.quantity-container {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-quantity-container.active {
  background-color: lightgreen;
}

.add-quantity-container {
  height: 30px;
  width: 30px;
  border-radius: 50%;

  background-color: #eee8db;

  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 1s, color 1s;
  transition: 1s;
}

.add-container p {
  margin-bottom: 5px;
  font-size: 20px;
}

.add-quantity-container:hover {
  transition: 0.5s;
  opacity: 0.6;
}

.add-quantity-container:active {
  opacity: 0.4;
  transition: opacity 0.3s;
}

