.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 100px;
  width: 100%;
  border-bottom: solid 0.5px rgb(180, 177, 177);
  padding: 20px 0px;
  box-sizing: border-box;
}

/*LEFT*/

.left-container {
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
  /*NYA*/
  min-width: 20%;
}

.company-name {
  margin-left: 20px;
  color: orange;
}

/*MIDDLE*/
.middle-container {
  display: flex;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 2;
}

.list-container {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 450px;
  flex-wrap: wrap;
}

.list-container a {
  text-decoration: none;
  color: black;
  font-size: 18px;
  font-family: "Roboto", system-ui;
  font-weight: 400;
  font-style: normal;

  text-decoration: none;
}

/*RIGHT*/

.right-section-header {
  height: 100%;
  margin-right: 30px;
  gap: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
  min-width: 15%;

  /*
width: 250px;

*/
}

.cart-img-container {
  height: 50px;
  width: 50px;
  position: relative;
}

.cart-img {
  height: 100%;
  width: 100%;
  cursor: pointer;
}

.tooltip-cart {
  position: absolute;
  background-color: orange;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  top: 6px;
  right: 7px;
  display: none;
}

.tooltip-cart.visible {
  display: block;
}

.profile-img-container {
  height: 30px;
  width: 30px;
}

.profile-img {
  height: 100%;
  width: 100%;
  cursor: pointer;
}

.sign-in-button-container {
  height: 45px;
  width: 95px;
}

.sign-in-button-container button {
  height: 100%;
  width: 100%;
  border-radius: 10px;
  border: none;
  background-color: red;
  color: white;

  cursor: pointer;
}

.sign-in-button-container button:hover {
  transition: 0.5s;
  opacity: 0.8;
}

.sign-in-button-container button:active {
  opacity: 0.5;
  transition: opacity 0.3s;
}

/*SIGN UP FORM*/

.sign-up-form {
  height: 420px;
  width: 370px;
  background-color: white;
  position: absolute;
  top: 25%;
  left: 36%;
  z-index: 10;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.container-1-sing-up {
  width: 100%;
  height: 13%;

  font-family: "Roboto", system-ui;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative !important;
}

.sign-up-container {
  width: 80% !important;
  height: 100%;
  display: flex;
  align-items: center;
}

.sign-up-container p {
  font-family: "Roboto", system-ui;
  font-weight: bold;
  font-style: normal;
  font-size: 25px;
  margin-left: 20px;
}

.delete-icon-container {
  width: 10% !important;
  position: absolute !important;
  right: 10px !important;
  top: 15px !important;
}

.delete-icon-container img {
  width: 80% !important;
  height: 80% !important;
  cursor: pointer;
}

.container-2-personal-information {
  width: 100%;
  height: 55%;
  display: flex;
  flex-direction: column;

  margin-bottom: 20px;

  justify-content: center;
  gap: 10%;
}

.container-2-personal-information input {
  margin-left: 20px;
  width: 89%;
  height: 20%;
  border: 0.5px gray solid;
  border-radius: 5px;
  outline: none;
  padding-left: 5px;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
}

.container-2-personal-information input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
}

.container-3-create-button {
  width: 100%;
  height: 11%;
  margin-bottom: 4%;
}

.container-3-create-button button {
  height: 100%;
  width: 89%;
  background-color: orange;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  margin-left: 20px;
  transition: background-color 1s, color 1s;
}

.container-3-create-button button:hover {
  transition: 0.5s;
  opacity: 0.8;
}

.container-3-create-button button:active {
  opacity: 0.5;
  transition: opacity 0.3s;
}

.container-4-already-account {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 10%;
}

.already-account-p {
  font-family: "Open Sans", sans-serif;
  margin-left: 20px;
  font-size: 15px;
}

.click-here-p {
  font-family: "Open Sans", sans-serif;
  color: orange;
  font-size: 16px;
  cursor: pointer;
}

/*SIGN IN FORM*/

.sign-in-form {
  height: 380px;
  width: 350px;
  background-color: white;
  position: absolute;
  top: 25%;
  left: 36%;
  z-index: 10;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.container-1-sing-in {
  width: 100%;
  height: 13%;

  font-family: "Roboto", system-ui;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.sign-in-container {
  width: 90%;
  height: 100%;

  display: flex;
  align-items: center;
}

.sign-in-container p {
  font-family: "Roboto", system-ui;
  font-weight: bold;
  font-style: normal;
  font-size: 25px;
  margin-left: 20px;
}
