


.filter-section-container{
height: 360px;
width: 100vw;
margin-bottom: 30px;
}


.explore-menu-p{
font-family: "Poppins", sans-serif;
font-style: normal;
font-size: 30px;
margin-left: 100px;
margin-bottom: 30px;
}


.explore-menu-p-2{
width: 100vw;
}

.explore-menu-p-2 p{
font-family: "Poppins", sans-serif;
margin-left: 100px;
margin-bottom: 30px;
font-size: 17px; 
}


.filter-product-container{    
width: 86vw;
height: 215px;

border-bottom: 1px solid gray;

padding-bottom: 30px;
display: flex;
flex-direction: row;
gap: 30px;
margin-left: 100px;
}


.product-container{
display: flex;
flex-direction: column; 
width: 130px;
align-items: center;
height: 200px;
}


.product-img-container{
height: 125px;
width: 125px;
margin-bottom: 15px;

}

.product-container:nth-child(1){
  margin-left: 10px;
}



.product-img-container img {
height: 100%;
width: 100%;
border-radius: 70%;
cursor: pointer;
}


.product-name-container{
cursor: pointer;
}


.product-name-container p{
font-family: "Poppins", sans-serif;
font-size: 17px;
}









/*Media screen*/




@media (max-width: 1350px) {
  .filter-product-container{
   
     
    border: none;
   margin-left: 60px;
  }


  .explore-menu-p{
    margin-left: 60px;
  }
  
  .explore-menu-p-2 p{
    margin-left: 60px;
  }
}






